*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

#root{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    background: rgb(200, 226, 226);
}

.board-container{
   position: relative; 
   width: 700px;
   height: 700px;
   background-color: rgb(241, 241, 241);
}

.chess-board{
    height: 640px;
    width: 640px;
    display: flex;
    flex-direction: column-reverse;
    /* flex-direction: row; */
    position: absolute;
    top: 30px;
    left: 30px;
    background: rgb(234, 202, 231);
    margin: auto;
    border-radius: 7px;
    overflow: hidden;
}

.board__downnumb{
    width: 640px;
    /* position: absolute; */
    display: flex;
    position: absolute;
    left: 30px;
    bottom: 10px;
    justify-content: center;
    align-items: center;
}
.board__downnumb span{
    width: 80px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}

.board__leftnumb{
    position: absolute;
    top: 30px;
    display: flex;
    left: 15px;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    height: 640px;
}
.board__leftnumb span{
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 600;  
}
.board-cell{
    width: 80px;
    height: 80px;
    cursor: pointer;
}

.board-cell._white{
    
    background-color: rgb(244, 233, 219);
}
.board-cell._black{

    background-color: rgb(10, 147, 147);
}

.cells-row{
    display: flex;
    width: 640px;
    height: 80px;
    flex-direction: row;
}
.board-cell._active .rook,
.board-cell._active .pawn,
.board-cell._active .queen,
.board-cell._active .knight,
.board-cell._active .king,
.board-cell._active .bishop
{
    background-color: rgba(217, 205, 31, 0.5);
}
.rook, .pawn, .queen, .knight, .king, .bishop{
    height: 80px;
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    cursor: pointer;
    z-index: 2;
}

.rook img, .pawn img, .queen img, .knight img, .king img, .bishop img{
    height: 70px;
    width: 70px;
    z-index: 1;
    pointer-events: none;
}